<template>
    <div class="user" style="line-height:25px">
        <el-row>
            <el-col :span="21">
                <!-- 模糊检索 -->
                <div class="grid-content bg-purple">
                    <el-form :inline="true" :model="userFormLike" class="demo-form-inline">
                        <el-form-item label="登录用户名">
                            <el-input v-model="userFormLike.loginUserAme" placeholder="请输入登录用户名" clearable></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search">查询</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
        </el-row>
        
        <!--功能列表-->
        <el-row>
            <el-table :data="tableData" :border="true" class="tableBox" style="width: 100%">
                <el-table-column prop="loginUserAme" label="登录用户名字" ></el-table-column>
                <el-table-column prop="lastLoginTime" label="最后一次登录时间" ></el-table-column>
            </el-table>
        </el-row>

        <!--分页功能-->
        <el-row>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20, 100, 200, 300, 400]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </el-row>

    </div>
</template>

<script>

  export default {
    data() {
      return {
        userFormLike:{
            loginUserAme: ''
        },
        tableData: [],
        currentPage: 1,
        pageSize: 5,
        total: 100
      }
    },
    components: {
        // Upload
    },
    created: function(){
        this.load();  //页面初始化时，加载的方法
    },
    methods: {
        //初始化数据
        load() {
            var _this = this;
            this.$http({
                method: 'post',
                url: '/UserLogin/getPage',
                data: {pageCode:this.currentPage,pageSize:this.pageSize,loginUserAme:this.userFormLike.loginUserAme}
            }).then(res => {
                if (res.status == 200) {
                    _this.tableData = res.data.list;
                    _this.total = res.data.total;
                }
                
            });
        },
        //模糊检索
        search() {
            this.load();
        },
        //每页显示数量发生变化
        handleSizeChange(val) {
            this.pageSize = val;
            this.load();
        },
        //第几页发生变化
        handleCurrentChange(val) {
            this.currentPage = val;
            this.load();
        }
    }
  }
</script>

<style>
  .bg-purple{
      text-align: left;
  }
  .bg-purple-light{
      text-align: right;
  }
</style>